import React, {useState} from "react";
import LoginView from "./view/LoginView";
import axios from "axios";
import Cookies from "js-cookie";
import {useAuthStore} from '../stores/store';
import {useNavigate } from 'react-router-dom';

/**
 * LoginContainer
 * ,   
 * , , API
 * */
const LoginContainer = () => {
    const { username, setUsername, password, setPassword, errorMessage, setErrorMessage }
= useAuthStore();

    const navigate = useNavigate();
    /**
     * /login 
     * @param username
     * @param password
     * */
    const handleLogin = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            const response = await axios.post('http://localhost:3000/login', {
                username: username,
                password: password
            });

            if (response.status === 200) {
                //    
                const data = response.data;
                const token = data.accessToken; //  accessToken

                //   
                Cookies.set('token', token,{secure:true, sameSite: 'none'});
                console.log('token,data : ' + token);
                //   secureLogin 
                await secureLogin(token); //await -> secureLogin    
                navigate('/MainView', data);
            } else {
                //   
                console.error(' :', response.statusText);
            }
        } catch (error) {
            console.error(' :', error);
            setErrorMessage('');
        }
    };

    /**
     *  /secure 
     *  @param token 
     **/
    const secureLogin = async (token: string) => {
        try{
            //   
            const headers = {
                'Authorization': `Bearer ${token}`
            };

            //  GET  
            const response = await axios.get('http://localhost:3000/secure', { headers });

            //    
            if (response.status === 200) {
                console.log(' :', response.data);
                console.log('..')
            }

        } catch (error) {
            //    
            console.error('     :', error);
        }
    };

    return(
        <LoginView
            username={username}
            setUsername={setUsername}
            password={password}
            setPassword={setPassword}
            errorMessage={errorMessage}
            handleLogin={handleLogin}
        />
    );
};

export default LoginContainer;